@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Otomanopee+One&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
		'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
		'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background: linear-gradient(to right, #0f2027, #203a43, #2c5364);
	text-align: center;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

:root {
	--yellow: #f9a602;
	--yellow2: #ffff99;
	--white: #ffffe1;
}
.App {
	margin: auto;
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-family: 'Roboto', sans-serif;
}

.title {
	font-family: 'Otomanopee One', sans-serif;
	color: #fff;
	padding: 1em;
	letter-spacing: 3px;
	text-shadow: 0 0 5px rgb(255, 145, 0), 3px -1px 5px rgb(255, 238, 0);
}

body {
	color: #222;
}

.todo-input {
	min-width: 15rem;
	width: 40vw;
	max-height: 2.5rem;
	background-color: #ffffe1;
	background-color: var(--white);
	border: none;
	outline: none;
	border-radius: 5px;
	padding: 0.5rem 1rem;
	align-self: center;
	line-height: 1.75;
}

.todo-input:focus {
	outline: none;
	border: 2px solid rgb(226, 150, 8);
}

.addTodos {
	display: flex;
	justify-content: center;
}

.add-btn {
	margin-left: 1rem;
	background-color: #f9a602;
	background-color: var(--yellow);
	border-radius: 50%;
	border: 2px solid #e1ebfd;
	font-size: 1.5rem;
	width: 2.8rem;
	height: 2.8rem;
	cursor: pointer;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: transform 0.25s;
	color: #e1ebfd;
}

.add-btn:hover {
	transform: scale(120%);
}
.displaytodos {
	margin-top: 3rem;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.displaytodos .buttons {
	margin-bottom: 2rem;
}

.displaytodos .buttons button {
	background-color: #ffffe1;
	background-color: var(--white);
	padding: 0.5rem 1.2rem;
	border-radius: 20px;
	cursor: pointer;
	border: none;
}

.displaytodos .buttons button:focus {
	outline: none;
}

.displaytodos .buttons button:hover {
	box-shadow: 0 0 5px rgb(255, 145, 0), 3px -1px 5px rgb(255, 238, 0);
}

.displaytodos .buttons button:not(:last-child) {
	margin-right: 1rem;
}

.displaytodos ul {
	list-style: none;
	display: flex;
	align-self: flex-start;
	flex-wrap: wrap;
	margin-left: 5%;
}

.card {
	display: flex;
	flex-direction: column;
	background: #ffffe1;
	background: var(--white);
	margin: 0 1rem 1rem 0;
	height: 8rem;
	width: 18rem;
	border-radius: 0.5rem;
	padding: 1rem;
	position: relative;
}

.card textarea {
	padding: 0.5rem;
	border-radius: 8px;
	border: none;
	background-color: transparent;
	resize: none;
	outline: none;
	color: #271c6c;
	height: 70%;
}
.card textarea:disabled {
	background-color: transparent;
}

.card .btns {
	position: absolute;
	bottom: 1rem;
	right: 1rem;
}

.card .btns button {
	border-radius: 20%;
	border: none;
	margin: 0 0.6rem;
	font-size: 1.4rem;
	cursor: pointer;
	color: #271c6c;
	background-color: #ffffe1;
	background-color: var(--white);
}

.card .btns button:focus {
	outline: none;
}

.completed {
	position: absolute;
	right: 0.3rem;
	top: 0.3rem;
	background-color: #ffffe1;
	background-color: var(--white);
	border: 2px solid #272727;
	color: #e1ebfd;
	font-size: 0.8rem;
	padding: 0.3rem 1rem;
	border-radius: 20px;
}

@media Screen and (max-width: 640px) {
	.displaytodos {
		overflow: hidden;
		margin-top: 2rem;
	}
	.displaytodos ul {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-left: 0;
		align-self: center;
	}
	.card {
		margin-right: 0;
	}
}

